// src/components/Calculator/CalculatorServer.jsx
import React from 'react';
import Calculator from './Calculator';

// Статичный список моделей (вручную вставленный)
const models = [
  { id: 5, name: '7', brand: { id: 2, name: 'BMW' } },
  { id: 7, name: '5', brand: { id: 2, name: 'BMW' } },
  { id: 8, name: '4', brand: { id: 2, name: 'BMW' } },
  { id: 9, name: '3', brand: { id: 2, name: 'BMW' } },
  { id: 10, name: '2', brand: { id: 2, name: 'BMW' } },
  { id: 11, name: '1', brand: { id: 2, name: 'BMW' } },
  { id: 12, name: 'X', brand: { id: 2, name: 'BMW' } },
  { id: 13, name: 'M', brand: { id: 2, name: 'BMW' } },
];

export const revalidate = 60; // ISR: обновляем раз в минуту

export default async function CalculatorServer() {
  // 1) Подгружаем только подмодели и серии с API
  const [submodelsRes, seriesRes] = await Promise.all([
    fetch('http://89.104.65.124/api/submodels/', { next: { revalidate: 60 } }),
    fetch('http://89.104.65.124/api/series/', { next: { revalidate: 60 } }),
  ]);

  if (![submodelsRes, seriesRes].every(r => r.ok)) {
    throw new Error('Не удалось загрузить данные submodels или series');
  }

  const [submodels, series] = await Promise.all([
    submodelsRes.json(),
    seriesRes.json(),
  ]);

  // 2) Собираем структуру data из моделей + связей
  const data = models.map(m => ({
    ...m,
    submodels: submodels
      .filter(sm => sm.model.id === m.id)
      .map(sm => ({
        ...sm,
        series: series.filter(s => s.model.id === sm.id),
      })),
  }));

  // 3) Услуги пока можно оставить заглушкой или добавить позже
  const services = [];

  // 4) Передаём данные в клиентский компонент
  return <Calculator data={data} services={services} />;
}
